.SingleController {
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 5px 0;
}

.SingleController button {
    display: block;
    font: inherit;
    padding: 5px;
    margin: 0 5px;
    width: 80px;
    border: 1px solid #AA6817;
    cursor: pointer;
    outline: none;
}

.SingleController button:disabled {
    background-color: #AC9980;
    border: 1px solid #7E7365;
    color: #ccc;
    cursor: default;
}

.SingleController button:hover:disabled {
    background-color: #AC9980;
    color: #ccc;
    cursor: not-allowed;
}

.Label {
    text-transform: capitalize;
    padding: 10px;
    font-weight: bold;
    width: 80px;
}

.SingleController .Less {
    background-color: #D39952;
    color: white;
}

.SingleController .More {
    background-color: #8F5E1E;
    color: white;
}

.SingleController .Less:hover, .SingleController .Less:active {
    background-color: #DAA972;
    color: white;
}

.SingleController .More:hover, .SingleController .More:active {
    background-color: #99703F;
    color: white;
}